body {
    margin: 0;
    padding: 0;
    font-family: "Do Hyeon", sans-serif;
    font-weight: 200;
    background-color: #eff3f7;
    height: 100%;
}
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}
.input__movieInfo {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
    font-size: 30px;
}
input {
    width: 600px;
    margin: 0;
    height: 51px;
    background: none;
    border-bottom: 1px solid #d63461;
    border-radius: 0;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    padding: 0 5px;
    position: relative;
}
/*.input__div {
    text-align: center;
}*/

.nav {
    z-index: 1;
    position: fixed;
    top: 50px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 10px 20px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
}

@media screen and (max-width: 1090px) {
    .nav {
        left: auto;
        left: initial;
        top: auto;
        top: initial;
        bottom: 0px;
        width: 100%;
    }
}

.nav a {
    text-decoration: none;
    color: #283747;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}

.nav a:not(:last-child) {
    margin-bottom: 20px;
}

body * {
    box-sizing: border-box;
}
