.info__pageCover {
    height: 100vh;
    display: flex;
    align-items: center;
}
.info__backdropimage {
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}
.info__backdropimage {
    width: 100%;
    position: relative;
    /* z-index: -1; */
}
.info__backdropimage .info__customBg {
    background-image: linear-gradient(
        to right,
        rgba(17.25%, 16.47%, 20.78%, 0.8) 10px,
        rgba(25.49%, 25.1%, 25.88%, 0.7) 100%
    );
    height: 100vh;
}
div.info__customBg {
    display: flex;
    padding-left: 100px;
    flex-wrap: wrap;
    align-items: center;
}
.info__container {
    padding: 20px;
    border-radius: 5px;
    color: white;
    width: 100%;
    max-width: 1800px;
    font-weight: 300;
    display: flex;
    justify-items: center;
}
.info__poster__wrapper {
    height: auto;
    border-width: 0px;
    min-width: 300px;
    width: 300px;
    height: 450px;
    overflow: hidden;
    border-radius: 20px;
}
.info__poster {
    display: block;
    min-width: 300px;
    width: 300px;
    height: 450px;
    position: relative;
    top: 0;
    left: 0;
}
.info__singCol {
    width: 1800px;
}
.info__title {
    width: 100%;
    font-weight: 300;
}
.info__tagline {
    width: 100%;
    font-style: italic;
    font-weight: 100;
    opacity: 60%;
}

.info__overview {
    width: 100%;
    font-size: 1em;
    width: 100%;
}
.info__container .info__data {
    box-sizing: border-box;
    width: 550px;
    padding-left: 40px;
}
.info__genres {
    display: block;
}
.info__release__date {
    display: block;
}
.info__reate {
    display: block;
}
.trailer__wrap {
    display: relative;
    padding-left: 50px;
}
.trailer__wrap iframe {
    /* padding-top: 56.25%; 720 / 1280 = 0.5625 */
    z-index: 200;
}
.trailer__wrap h1 {
    text-align: center;
}

